.homeDiv {
  height: 600px;
  padding-top: 100px;
}

hr {
  border: 3px solid #545fc4;
  margin: 100px 100px 0px 100px;
  background-color: #545fc4;
}

.container {
  display: flex;
}

.start {
  width: 100%;
  height: 100%;
  border-radius: 15px;
}

.leftContainer {
  height: 330px;
  width: 300px;
  margin-top: 130px;
  margin-left: 350px;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.rightContainer {
  font-size: 24px;
  margin: 100px 0px 0px 200px;
  color: rgb(232, 225, 225);
  font-family: "Roboto Slab", serif;
  line-height: 50px;
}

.resumeBtn {
  padding: 0.8em 1.8em;
  border: 3px solid #545fc4;
  position: relative;
  overflow: hidden;
  background-color: transparent;
  text-align: center;
  font-size: 16px;
  font-weight: bold;
  transition: 0.3s;
  z-index: 1;
  font-family: inherit;
  color: #545fc4;
  margin-top: 20px;
  cursor: pointer;
}

.resumeBtn::before {
  content: "";
  width: 0;
  height: 300%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  background: #545fc4;
  transition: 0.5s ease;
  display: block;
  z-index: -1;
}

.resumeBtn:hover {
  color: white;
}

.resumeBtn:hover::before {
  width: 105%;
}

@media (min-width: 320px) and (max-width: 480px) {
  .homeDiv {
    width: 1051px;
    margin-bottom: 600px;
  }
  .container {
    display: grid;
    grid-template-columns: repeat(1, 1051px);
  }
  .rightContainer {
    text-align: center;
    margin: auto;
    margin-top: 100px;
    font-size: 2.5rem;
  }
  .resumeBtn {
    height: 120px;
    width: 340px;
    font-size: 2rem;
  }
  .leftContainer {
    margin: auto;
    margin-top: 150px;
    width: 800px;
  }
  .start {
    width: 400px;
    height: 450px;
  }
  hr {
    margin-top: 150px;
    width: 850px;
  }
}
