.card {
  width: 350px;
  height: 450px;
  background-image: linear-gradient(163deg, #4c04b8 0%, #42b3f4 100%);
  border-radius: 20px;
  transition: all 0.3s;
}

.card2 {
  width: 350px;
  height: 450px;
  background-color: #131010;
  border-radius: 20px;
  transition: all 0.2s;
}

.card2:hover {
  transform: scale(0.98);
  border-radius: 20px;
}

.card:hover {
  box-shadow: 0px 0px 30px 1px rgba(39, 162, 203, 0.3);
}

.cardImage {
  width: 85%;
  margin: 28px 0px 0px 28px;
  border-radius: 10px;
  height: 170px;
}

.para {
  color: white;
  text-align: center;
  margin: 0px 20px;
}

.tsImg {
  display: flex;
  justify-content: center;
  margin-top: 5px;
}

.uiverse {
  padding: 0.8em 1.8em;
  border: 1px solid #545fc4;
  position: relative;
  overflow: hidden;
  background-color: transparent;
  text-align: center;
  font-size: 16px;
  transition: 0.3s;
  z-index: 1;
  font-family: inherit;
  color: #545fc4;
  margin: 25px 0px 0px 46px;
  cursor: pointer;
}

.uiverse::before {
  content: "";
  width: 0;
  height: 300%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  background: #545fc4;
  transition: 0.5s ease;
  display: block;
  z-index: -1;
}

.uiverse:hover {
  color: white;
}

.uiverse:hover::before {
  width: 105%;
}

.projectDiv {
  height: 600px;
}

.heading {
  color: #545fc4;
  font-size: 40px;
  font-family: "Roboto Slab", serif;
  text-decoration: underline;
  text-align: center;
}

.container {
  width: 80%;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.cards {
  height: 350px;
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

@media (min-width: 320px) and (max-width: 480px) {
  .projectDiv {
    width: 1051px;
    height: auto;
    margin-top: 150px;
  }
  .container {
    height: auto;
    width: 1050px;
    display: flex;
    flex-direction: column;
  }
  .heading {
    font-size: 5rem;
  }
  .cards {
    height: 1100px;
    margin-bottom: 500px;
  }
  .card {
    height: 1500px;
    width: 900px;
    display: flex;
    justify-content: center;
  }
  .card2 {
    height: 1500px;
    width: 900px;
    border-radius: 20px;
  }
  .para {
    font-size: 3rem;
  }
  .cardImage {
    width: 84%;
    height: 500px;
    margin: 70px 75px;
    border-radius: 20px;
  }
  .tsImg img {
    width: 150px;
    height: 250px;
  }
  .uiverse {
    width: 380px;
    padding-bottom: 120px;
    height: 100px;
    font-size: 3rem;
  }
}
